.list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}

.list .bgimg {
    position: relative;
    border-radius: 16px;
    border: 1px solid #959595;
    background-color: white;
    overflow: hidden;
    aspect-ratio: 1/1;
    width: 100%;
    -webkit-transition: border 0.2s, border-radius 0.2s;
    transition: border 0.2s, border-radius 0.2s;
}

.list .bgimg img {
    -o-object-fit: contain;
    object-fit: contain;
    -o-object-position: center;
    object-position: center;
    -webkit-text-emphasis: center;
    text-emphasis: center;
    height: 100%;
    width: 100%;
}

.list h4 {
    line-height: 1.5;
    color: #1a1a1a;
    margin: 16px 0 10px;
}

.list .more {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    font-family: "VT323", monospace;
    color: white;
    font-size: 1.5rem;
    border-radius: 99em;
    background-color: rgba(247, 174, 33, 0.8);
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 66%;
    height: 66%;
    opacity: 0;
    -webkit-transition: opacity 0.6s;
    transition: opacity 0.6s;
}

@media (max-width: 768px) {
    .list .bgimg {
        border-radius: 8px;
    }
}

@media (min-width: 769px) {
    .list a {
        -webkit-transition: 0s;
        transition: 0s;
    }

    .list a:hover .bgimg {
        border: 4px solid #f7ae21;
        border-radius: 200px;
    }

    .list a:hover .more {
        opacity: 1;
    }
}

footer {
    margin-top: 0;
}

#Wapper {
    padding-top: 0;
}

.swiper-pagination.swiper-pagination-horizontal .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    border: 1px solid #959595;
    background-color: transparent;
    position: relative;
    margin: 0 8px;
}

.swiper-pagination.swiper-pagination-horizontal .swiper-pagination-bullet:before {
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    top: -6px;
    left: -6px;
    border: 1px solid #f7ae21;
    border-radius: 99em;
    opacity: 0;
}

.swiper-pagination.swiper-pagination-horizontal .swiper-pagination-bullet-active.swiper-pagination-bullet {
    border-color: #f7ae21;
    background-color: #f7ae21;
}

.swiper-pagination.swiper-pagination-horizontal .swiper-pagination-bullet-active.swiper-pagination-bullet:before {
    opacity: 1;
}

.p1 {
    height: 100vh;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding-top: 100px;
    padding-bottom: 100px;
    margin-bottom: 100px;
}

.p1 .swiper-slide {
    width: 640px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.p1 .swiper-slide a {
    display: block;
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
}

.p1 .swiper-slide img {
    object-fit: cover;

    border: 1px solid #d8d8d8;
    border-radius: 80px;
    -webkit-transform: translateX(20%) translateY(16%) rotate(-15deg) scale(0.5);
    transform: translateX(20%) translateY(16%) rotate(-15deg) scale(0.5);

    transition: all 0.3s ease-in-out;
}

.p1 .swiper-slide-active img {
    -webkit-transform: scale(1);
    transform: scale(1);
}

.p1 .swiper-slide.swiper-slide-prev img {
    -webkit-transform: rotate(-10deg) scale(0.5);
    transform: rotate(-10deg) scale(0.5);
}

.p1 .swiper-slide.swiper-slide-next img {
    -webkit-transform: rotate(10deg) scale(0.5);
    transform: rotate(10deg) scale(0.5);
}

.p1 .swiper-slide.swiper-slide-next+div img {
    -webkit-transform: translateX(-20%) translateY(16%) rotate(15deg) scale(0.5);
    transform: translateX(-20%) translateY(16%) rotate(15deg) scale(0.5);
}

.p1 .swiper-slide-active img {
    border: 8px solid #f7ae21;
}

.p1 .swiper-pagination-bullets.swiper-pagination-horizontal {
    bottom: 10vh;
}

.p1 .w80-yel {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    z-index: 20;
}

.p1 .arrow-left {
    left: 25%;
}

.p1 .arrow-right {
    right: 25%;
}

.p1 .marquee {
    position: absolute;
    left: 0;
}

.p1 .marquee.top {
    top: 20vh;
}

.p1 .marquee.btm {
    bottom: 10vh;
}

.p1 .scrdown {
    position: absolute;
    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    text-align: center;
    font-family: "VT323", monospace;
    font-size: 1.25rem;
    color: #f7ae21;
}

.p1 .scrdown img {
    width: 16px;
    margin-top: 6px;
}

@media (min-width: 769px) and (max-width: 1439px) {
    .p1 .arrow-left {
        left: calc(50% - 45vh);
    }

    .p1 .arrow-right {
        right: calc(50% - 45vh);
    }
}

@media screen and (min-height: 600px) and (max-height: 900px) and (min-width: 769px) {
    .p1 {
        padding-top: 150px;
    }

    .p1 .swiper-pagination-bullets.swiper-pagination-horizontal {
        bottom: 5vh;
    }

    .p1 .scrdown {
        bottom: -50px;
    }

    .p1 .swiper-slide {
        width: 70vh;
    }
}

